.quest-details-list{
	margin-top: 0 !important;

}
.quest-details {
	background: $app-white;
	min-width: 1300px;
	min-height: calc(100vh - 65px);

	.quest-empty{
		margin-top: 40px;
		.no-achievers{
			margin: 0 auto;
			width: 200px;
			display: block;
		}
		.empty_msg{
			margin: 30px;
			text-align: center;
			font-size: 16px;
		}

	}

	.arcade-list-wrapper {
		margin-left: 16px;
		margin-right: 16px;

		.quest-msg-container {
			font-size: $text-large;
			color: $app-grey;
			line-height: 28px;
			padding-top: 32px;
			padding-bottom: 16px;
			background: url('/a/assets/images/module-quest/achieved.svg') 85% 15px no-repeat;

			b {
				margin-left: 5px;
				font-weight: $text-weight-semi-bold;
			}

			p {
				margin-bottom: 6px;

				&.motiv-msg {
					margin-bottom: 0;
				}
			}
		}


		.arcade-container {
			display: flex;
			flex-wrap: wrap;
			position: relative;
			z-index: 2;

			.new-quest-container {
				display: flex;
				width: 49%;
				float: left;
				padding: 0;
				border: 1px solid $border-light-color;
				box-sizing: border-box;
				box-shadow: 0px 1px 6px $box-shadow-color-1;
				border-radius: 8px;
				margin: 8px;
				position: relative;

				&:nth-child(-n + 2) {
					margin-top: 0;
				}

				&:nth-child(odd) {
					margin-left: 0;
				}

				.inner-wrap {
					margin: 25px 32px 25px 32px;

					.lock {
						background: $app-white;
						border: 1px solid $background-color;
						box-sizing: border-box;
						border-radius: 4px;
						height: 24px;
						width: 24px;
						text-align: center;
						position: absolute;
						top: 58px;
						left: 64px;
						box-shadow: 0px 1px 6px $box-shadow-color-1;

						svg {
							height: 12px;
							width: 10.5px;
							position: relative;
							top: 2px;
							right: 14px;
						}

					}
				}

				.arcade-panel-outer {
					margin: 25px 32px 25px 0;
				}

				.panel-row {
					padding: 12px 20px;
				}

				.panel-outer-card {
					width: 98%;
					font-size: 14px;
					color: $app-grey;
					margin: 0;

					h2 {
						font-weight: 600;
						width: 100%;
						min-width: 216px;
						font-size: 14px;
						max-height: 34px;
						overflow: hidden;
					}

					.help-text {
						margin-top: 13px;
						color: $primary-text-color-1;
					}
				}
			}

			.badges {
				width: 48px;
				height: 54px;
				margin: 0;
				&.filtered{
					opacity: 0.4;
				}
			}

			.points {
				background: $background-color;
				color: $alertCancelColor;
				padding: 5px 8px;
				display: block;
				margin-top: 8px;
				text-align: center;

			}
		}
		&.filtered{
			.arcade-container{
				background: none;
			}
		}
	}
}
